import React from "react";
import styles from "./Card.module.css";

const Card = (props) => {
  // const logo = require(props.imgSrc);

  return (
    <section className={styles.card}>
      <p className={styles.card_title}>{props.title}</p>
      <img src={props.imgSrc} alt={props.imgAlt}></img>
      <p className={styles.card_description}>{props.desc}</p>
      <div class={styles.card_link_container}>
        <button class={styles.card_link_button}>
          <a href={props.liveLink} class={styles.card_link_livedemo}>
            Demo
          </a>
        </button>
        <button class={styles.card_link_button}>
          <a href={props.demoLink} class={styles.card_link_source}>
            GitHub
          </a>
        </button>
      </div>
    </section>
  );
};

export default Card;
