import React from "react";
import styles from "./Contact.module.css";

const Contact = () => {
  return (
    <section id="contact" className={styles.contact}>
      <p className={styles.contact_desc}>
        If you want write to me or ask something, go ahead...
      </p>
      <div className={styles.contact_mail_container}>
        <i className="far fa-envelope"></i>
        <p>kamil.wojtol97@gmail.com</p>
      </div>
      <p className={styles.contact_desc}>
        ...or you can write to me on these sites!
      </p>
      <div className={styles.contact_other}>
        <ul className={styles.contact_list}>
          <li className={styles.contact_item}>
            <a href="https://github.com/kamilwojtol">
              <button className={styles.contact_social}>
                <i class="fab fa-github"></i>
              </button>
            </a>
          </li>
          <li className={styles.contact_item}>
            <a href="https://twitter.com/kwojtol">
              <button className={styles.contact_social}>
                <i class="fab fa-twitter"></i>
              </button>
            </a>
          </li>
          <li className={styles.contact_item}>
            <a href="https://www.linkedin.com/in/kamil-wojtol/">
              <button className={styles.contact_social}>
                <i class="fab fa-linkedin"></i>
              </button>
            </a>
          </li>
        </ul>
      </div>
    </section>
  );
};

export default Contact;
