import React from "react";
import styles from "./Projects.module.css";
import todo from "../img/todo.png";
import olios from "../img/olios.png";
import clock from "../img/clock.png";

import Card from "./Card";

const Projects = () => {
  return (
    <section id="projects" className={styles.projects}>
      <h3 className={styles.projects_title}>
        Projects I made and worked with:
      </h3>
      <div className={styles.projects_container}>
        <Card
          title={"To Do List"}
          imgSrc={todo}
          imgAlt="Todo logo"
          desc="Simple app for completing tasks. Made with HTML, CSS and JS."
          liveLink="https://kamilwojtol.github.io/todo/"
          demoLink="https://github.com/kamilwojtol/todo"
        />
        <Card
          title={"Olios"}
          imgSrc={olios}
          imgAlt="Olios logo"
          desc="Recreation of Olios template by symu.co. Made with HTML, CSS and JS."
          liveLink="https://kamilwojtol.github.io/olios/"
          demoLink="https://github.com/kamilwojtol/olios"
        />
        <Card
          title={"Clock"}
          imgSrc={clock}
          imgAlt="Clock logo"
          desc="Clock with greeting and random quote. Made with HTML, CSS and JS."
          liveLink="https://kamilwojtol.github.io/clock/"
          demoLink="https://github.com/kamilwojtol/clock"
        />
      </div>
    </section>
  );
};

export default Projects;
